as not been employed in many areas which it is needed.")]),_vm._v(" "),_c('li',[_c('strong',[_vm._v("Excess clothing/supplies (conversion to a market) (Tom's):")]),_vm._v(" People tend to send clothes during humanitarian crisis, and despite their good intentions, the reality is that most victims need medicine or hygiene products, over a new set of clothes. The wave of clothes that are donated is commonly dubbed \"the second disaster\". One problem with Tom's buy one send one policy, was the fact that it hurt small businesses by pushing them out of the market with free and better quality products.")]),_vm._v(" "),_c('li',[_c('strong',[_vm._v("Agriculture/loss of land (Easier to set up vertical farming):")]),_vm._v(" The loss of land/agriculture is a consistent issue for farmers during crisis. Vertical farming has been used as a method during humanitarian crisis to grow crops as it is more effective during times where water is not easily accessible. However, current methods aren't as cost efficient or effective, and do not allow production anywhere near where they were prior to a disaster.")]),_vm._v(" "),_c('li',[_c('strong',[_vm._v("Drones payload:")]),_vm._v(" Despite the convenience of drones, payload has been a big issue and the topic of much research since the rise of drones. The issue stems that while there are drones such as octopods, which can deliver heavier loads, they are extremely costly. Companies such as Zipline have moved away from the conventional drone and into robotic planes, which is used to carry 1kg of blood to places in need.")]),_vm._v(" "),_c('li',[_c('strong',[_vm._v("Tons of garbage:")]),_vm._v(" The garbage issue exist as a bigger issue than simply as a humanitarian crisis. While tackling the entire garbage problem is out of our scope, we must considered ways waste is generated, specifically during a humanitarian crisis, and whether or not we have the capacity to reduce, reuse, and convert the waste into something useful.")]),_vm._v(" "),_c('li',[_c('strong',[_vm._v("What can we do with robotics?")]),_vm._v(" While all these problems undeniably have a solution, an important aspect of this is how we can apply robotics to it, and while this does come with limitations, it also opens the scope for greater possibilities.")])]),_c('br'),_vm._v("\n\t\t\t\t\t\t\tFiguring out the problem is the first step in this project, and is usually just as difficult as finding a solution. Members were asked to do research prior to coming to the meetings and give a short presentation on their findings. We discussed the existing solutions to such problems and possible ways to improve upon them. What we found to be the most difficult part was implementation of robotics, as some of these issues had solutions that were not robotics related. However, the team was still able to successfully identify possible solutions to these problems, incorporating robotics.\n\t\t\t\t\t\t")]),_c('br'),_c('br'),_vm._v(" "),_c('h2',[_vm._v("Discussed Robotic Solutions")]),_vm._v(" "),_c('p',[_c('ul',[_c('li',[_c('strong',[_vm._v("Sensor that detects fertile soil (farming):")]),_vm._v(" Loss of agriculture and destruction of land makes farming very difficult for people who rely on it as a source of income and food. A device that helps people detect fertile soil, would allow those in need to have a easier and more successful time farming. ")]),_vm._v(" "),_c('li',[_c('strong',[_vm._v("Detecting harmful chemicals on the road (trash):")]),_vm._v(" A big issue during humanitarian crisis is transportation, whether that be supplies or people. However, it is not always clear whether or not debris on the road is safe to be removed in the presence of humans. Such a sensor would allow humanitarian workers and others to traverse roads with peace of mind, and allow for the quicker clearing of roads.")]),_vm._v(" "),_c('li',[_c('strong',[_vm._v("Sensor that detects live people in rubble/Identifying people:")]),_vm._v(" Identifying people trapped below buildings has always been an issue. A bigger issue is when time is spent finding those who are already deceased. As unfortunate as it is, a humanitarian worker's time is precious and should be spent on trying to help those still alive. Although NASA has created a device that satisfies some of these needs, it still lacks mobility as well as wide range usage.")]),_vm._v(" "),_c('li',[_c('strong',[_vm._v("Convert excess clothes to something useful (Economic stability):")]),_vm._v(" Many small businesses are completely devastated after natural disasters or wartime conflicts. Such a tragedy not only strip a family of their income, but a market of its supplies. Because of that mass amounts of excess clothing that is shipped to those in need, so much so that most of it is simply burned or tossed aside, it holds opportunity to be recycled. Not only that if disaster victims could use a device that converts the clothing to something useful that was also marketable, it could also help them financially.")])]),_c('br'),_vm._v("\n\t\t\t\t\t\t\tSimplifying the different humanitarian issues allowed us to come up with more specific and applicable problems that could potentially scaled upward. Each member was given two topics to do continued research on. Next week we hope to be better versed on the discussed solutions which will allow us to make a better decision moving forward in deciding what kind of project we want to pursue.\n\t\t\t\t\t\t")]),_vm._v(" "),_c('br'),_c('br'),_c('br'),_vm._v(" "),_c('h1',[_vm._v("Meeting 2: Design and Rubble Research")]),_vm._v(" "),_c('h2',[_vm._v("Hexapods")]),_vm._v(" "),_c('p',[_vm._v("\n\t\t\t\t\t\t\tWe looked at "),_c('a',{attrs:{"href":"http://scholarworks.rit.edu/cgi/viewcontent.cgi?article=9761&context=theses"}},[_vm._v("this")]),_vm._v(" design for hexapods. For the movement of the hexapod, we looked to "),_c('a',{attrs:{"href":"https://mitpress.mit.edu/sites/default/files/titles/content/ecal13/978-0-262-31709-2-ch147.pdf"}},[_vm._v("this document")]),_vm._v(" for the creation of a MNN that controls the movement of the robot. Additionally, "),_c('a',{attrs:{"href":"http://www.wseas.org/multimedia/journals/circuits/2015/a3072609-151.pdf"}},[_vm._v("this paper")]),_vm._v(" explains how to miniaturize a hexapod. Unfortunately, this method of fabrication isn’t feasible as it’s mostly done in research labs at the moment. As such, as hexapods seem to require a lot of motors and the like, it seems as if we should focus not on miniaturizing the robot but making it move autonomously. We also looked into a "),_c('a',{attrs:{"href":"https://www2.eecs.berkeley.edu/Courses/EE147/"}},[_vm._v("class")]),_vm._v(" at UC Berkeley that teaches MEMS fabrication, and what we got from this is that we could move away from miniaturizing and instead focus on the control and movement of these robots (autonomously and in communication with other robots). "),_c('br'),_c('br'),_vm._v("After looking at this more closely, we decided that a hexapod design would not work well for our purposes because it is too mechanically complex, harder to scale down, and simply not what we were looking for in a robot that is meant to be small and flexible.\n\t\t\t\t\t\t")]),_c('br'),_c('br'),_vm._v(" "),_c('h2',[_vm._v("Amoeba-Like Robots")]),_vm._v(" "),_c('p',[_vm._v("\n\t\t\t\t\t\t\tThis "),_c('a',{attrs:{"href":"https://www.technologyreview.com/s/407603/amoebalike-robots-for-search-and-rescue/"}},[_vm._v("article")]),_vm._v(" discusses benefits of the design, including its ability to move over different kinds of terrain, its ability to move on all sides so it never needs to be “upright”, and the possibility of making it flexible so it can squeeze through tight spaces. Although not explained in detail, the article also mentions which direction the team is moving, which is to make the robot much more rugged. Lastly, it explains some open problems such as carrying and using sensors and attaching a power supply to the robot. "),_c('br'),_c('br'),_vm._v("\n\n\t\t\t\t\t\t\tA team at Virginia Tech has published a "),_c('a',{attrs:{"href":"http://mechanismsrobotics.asmedigitalcollection.asme.org/article.aspx?articleid=1484862"}},[_vm._v("article")]),_vm._v(" concerning the whole skin locomotion robot. It explains their design for the movement of the robot. Instead of using electric motors to pull the bands that run across the robot’s length, it instead uses rings that contract and expand on either side of the robot. The contraction and expansion pulls the band and moves the robot. The contraction process is complicated and probably not the best direction for our team if we were to go with the amoeba design. Mainly this is because the movement process is not as natural as simply pulling the bands along tracks and it requires more complicated materials than simple motors. "),_c('br'),_c('br'),_vm._v("\n\n\t\t\t\t\t\t\tOne issue that we saw with the design described in the above paper is directing the robot side to side. If we use a contraction process the bands all move simultaneously which means that it can only move backwards and forwards. This loss of freedom would not only hurt the robots ability to maneuver in its environment but also hurt the sensor data we would get. If we use motors it may be possible to move only one side of the robot which may generate a rotational movement. "),_c('br'),_c('br'),_vm._v("\n\n\t\t\t\t\t\t\tIn terms of carrying sensors we need to account for where these sensors can be placed. Since the robot has a toroid shape with an empty cavity in the center, any sensors inside the robot will only be able to look directly in front or behind the robot. Therefore the sensors would need to be placed outside of the robot but not in the path of the ‘skin’. This "),_c('a',{attrs:{"href":"http://www.sciencemag.org/news/2017/10/searching-survivors-mexico-earthquake-snake-robots"}},[_vm._v("paper")]),_vm._v(" details what the team that built the snake robot learned from helping out during the evacuation after the Mexico City earthquake. They detail some sensors that would be useful to search and rescue teams, including cameras, microphones, and gas sensors. They also talk about using a stereo camera and laser range finder to create a 3D image of the rubble. They used a wired connection with someone controlling the robot. They actually stated it took more than one person, since the images retrieved had to be interpreted as well. "),_c('br'),_c('br'),_vm._v("\n\n\t\t\t\t\t\t\tWe still need to do research on these topics:\n\t\t\t\t\t\t\t"),_c('ul',[_c('li',[_vm._v("How to transmit data through concrete. One way would be to just send a wired connection with each robot. Could also use audio signals.")]),_vm._v(" "),_c('li',[_vm._v("How to transmit between robots. Many ways would work since they have short range sensors in general. Audio could work as well, if the signal is unique.")]),_vm._v(" "),_c('li',[_vm._v("What sensors we should send down. A camera and radar seem most useful. Can they fit on an amoeba robot?")])]),_vm._v(" "),_c('br')]),_c('h2',[_vm._v("Amoebas and Whole Skin Locomotion")]),_vm._v(" "),_c('p',[_c('a',{attrs:{"href":"http://www.hizook.com/blog/2010/01/31/amoeba-whole-skin-locomotion-robots-ooze-right"}},[_vm._v("This")]),_vm._v(" describes the whole skin locomotion robots and has a video on Dr. Dennis Hong’s research. The second video on feasibility ("),_c('a',{attrs:{"href":"https://youtu.be/k5DKeDECcA4"}}),_vm._v(") details how an amoeba works and two potential robot designs. This amoeba robot seems promising -- it moves at 0.5 m/s and can maneuver through places that are half the diameter of itself. One of the hardest design problems is actuating the outer ectoplasm.\n\t\t\t\t\t\t")]),_c('br'),_vm._v(" "),_c('h2',[_vm._v("Blob Bot")]),_vm._v(" "),_c('p',[_c('a',{attrs:{"href":"https://spectrum.ieee.org/automaton/robotics/robotics-software/irobot-soft-morphing-blob-chembot"}},[_vm._v("This robot")]),_vm._v(" is a palm-sized shape shifting robot ("),_c('a',{attrs:{"href":"https://youtu.be/SbqHERKdlK8"}}),_vm._v("). It looks like a ball when it is at full size, but compresses down to a blob when it is deflated. Essentially, researchers can inflate parts of the outer skin to move in a certain direction. It rolls and changes shape, which could potentially be useful in maneuvering around small spaces. Instead of whole skin locomotion like an amoeba, this uses “jamming skin enabled locomotion”. This article is from 2009, but the end of the article looks toward the future where multiple robots might be combined together or sensors can be attached to the robot.\n\t\t\t\t\t\t")]),_c('br'),_vm._v(" "),_c('h2',[_vm._v("Robtotic Worm")]),_vm._v(" "),_c('p',[_c('a',{attrs:{"href":"https://phys.org/news/2011-07-giant-robotic-worm-mimics-elegans.html"}},[_vm._v("This robot")]),_vm._v(" uses side-to-side worm movements to move through rubble. Its inventor wants the worm to be able to move through different environments such as water, snow, and mud. Snake- or worm-like robots are typically propelled forwards by an 'ideal' wave that their control system has worked out in advance. When they meet an obstacle, the control system senses that something is hampering the way it wants to move and directs the robot to change its shape accordingly. A drawback is that this robot isn’t interested in its surroundings and just wants to move forward. It would be cool to see a robot that is cognizant of its surroundings while still maneuvering through tight spots.\n\t\t\t\t\t\t")]),_c('br'),_vm._v(" "),_c('h2',[_vm._v("Automatic detection of access holes in disaster rubble")]),_vm._v(" "),_c('p',[_vm._v("\n\t\t\t\t\t\t\tThis is a "),_c('a',{attrs:{"href":"http://ieeexplore.ieee.org/xpls/icp.jsp?arnumber=6719364"}},[_vm._v(" paper")]),_vm._v(" on hole detection in rubble to determine if canines or robots can enter for a search and rescue operation. This would allow rescuers to identify easy entry points. First, a hole is defined by its functionality as a means for accessing the interior; then, geometric and photometric features are used for hole detection. The team conducted experiments using RGB-D data collected over several disaster training facilities using a UAV.\n\t\t\t\t\t\t")]),_c('br'),_c('br'),_vm._v(" "),_c('h1',[_vm._v("Meeting 3: Design Research")]),_vm._v(" "),_c('h2',[_vm._v("Outer Layer Design Ideas")]),_vm._v(" "),_c('p',[_vm._v("\n\t\t\t\t\t\t\tOne design would be a sphere that encompasses the inner hardware. It could also feature ridges--either 2, 4, 8, or more evenly spaced out, or only one hemisphere that would allow for better traction and grip. Ridges would probably be hinges or completely stationary. "),_c('br'),_c('br'),_vm._v("\n\t\t\t\t\t\t\tThe other design would be a sphere with two tank treads that move independently of each other as well as the outside sphere. It would be slightly raised so that the only contact would be with the treads to the ground.\n\t\t\t\t\t\t")]),_c('br'),_vm._v(" "),_c('h2',[_vm._v("Outer Layer Material Choices")]),_vm._v(" "),_c('p',[_c('ul',[_c('li',[_vm._v("Polyurethane Foam: Flexible, smooth, and rubbery, this material allows our robot to have traction on the ground as well as mobility through tighter space. However, it would still be difficult to squish down and continue movement.")]),_vm._v(" "),_c('li',[_vm._v("Treads: These have good traction and are beneficial for movement with little fear of slippage due to their high coefficient of friction.")]),_vm._v(" "),_c('li',[_vm._v("Polycarbonate Plastic (PC): This is used for the movement of the Sphero. It can be clear, is impact resistant, and is very strong and durable. However, it is a bit slippery, and it would take a bit more engineering and thought for movement.")]),_vm._v(" "),_c('li',[_vm._v("Acrylonitrile-butadiene-styrene (ABS): The material is readily available so there is probably no need to purchase it, but it’s not as durable as PC and is more susceptible to wear and tear.")]),_vm._v(" "),_c('li',[_vm._v("PLA: This material is also readily available, and Moffitt Library has a supply of this for free. This has similar issues with ABS, and it deforms at around 90° Celsius, but that shouldn’t be too big of a concern.")])])]),_c('br'),_vm._v(" "),_c('h2',[_vm._v("Motors")]),_vm._v(" "),_c('p',[_vm._v("\n\t\t\t\t\t\tThe motors need to be relatively tiny. They should be able to run off a lower voltage if we want it to be powered off an Arduino or Raspberry Pi. We are probably looking at some type of drone motor, because they are generally lightweight and small in dimensions.\n\t\t\t\t\t")]),_c('br'),_c('br'),_vm._v(" "),_c('h1',[_vm._v("Meeting 4: Finalize Design and Discuss Sensors")]),_vm._v(" "),_c('p',[_vm._v("\n\t\t\t\t\t\t\t\t\tDuring this meeting we were able to finalize our design for our robot and discuss possible sensors that we wanted to include. Due to the difficulties of having the entire robot’s outer shell be a complete sphere and be attached to a tether, we decided on having a design featuring two separate hollow hemispheres which would act as the wheels. The hemispheres would move on independent motors, which would still allow it to have 360 degrees of motion using a tank-turn style of movement. Each hemisphere is attached to a motor shaft that sticks out of a centerpiece which will be stationary. The centerpiece will house the motors along with additional sensors that we choose to have. This way the wire could run directly into the centerpiece and its motion would be limited as long as it remains taut.\n\t\t\t\t\t\t\t\t\t"),_c('br'),_c('br'),_vm._v("\n\t\t\t\t\t\t\t\t\tThe sensors that we would like to incorporate are an accelerometer, encoders for the motors, and infrared sensors. We would be using the accelerometers and the encoders to return feedback of the robot’s movement and work alongside an encoder that controls the spool that gives slack to the tether. A system like this in theory would know when to give the robot more wire as to allow it to advance forward as well as a controlled descent if it falls into a pit.\n\t\t\t\t\t\t\t\t")]),_c('br'),_c('br'),_vm._v(" "),_c('h1',[_vm._v("Meeting 5: Stages of Development")]),_vm._v(" "),_c('p',[_vm._v("\n\t\t\t\t\t\t\t\t\tAfter talking to our advisor, we evaluated our goals and and created a timeline for our robot development. We decided that it would be best to focus on getting our robot to detect something in rubble and mark the location of the object. To simulate this, we can use a heat source as the object the robot will detect; a heat source is representative of a human body since human body temperatures are generally higher than the ambient temperature.\n\t\t\t\t\t\t\t\t\t"),_c('br'),_c('br'),_vm._v("\n\t\t\t\t\t\t\t\t\tWe devised 8 different stages of development. While we may not get to the final stages, we have a better idea of what we want to do now."),_c('br'),_vm._v(" "),_c('ol',[_c('li',[_vm._v("Create a maze for the robot. It will not be a direct path, but the terrain will be flat and smooth (e.g. a tabletop) and a person will be controlling the movement of the robot. The main purpose of this step is to test the drivetrain.")]),_vm._v(" "),_c('li',[_vm._v("Add more obstacles to the maze by changing the surface of the field. We will add materials such as sand, rocks, and wood chips to create bumps and ridges. The robot will be teleoperated, so the purpose of this step is to test the robot’s ability to get across rougher terrain.")]),_vm._v(" "),_c('li',[_vm._v("Create a vertical shaft. This will give us the opportunity to test our tether when the robot is traveling downward. This environment will not necessarily have any obstacles, as we simply want to test the process of lowering the robot and measuring the amount of vertical distance covered.")]),_vm._v(" "),_c('li',[_vm._v("We want to combine the three previous steps by creating a terrain that more closely resembles rubble. This rubble-like environment will have inclines, declines, and varied terrain. At this stage, the robot is still controlled by a person because we want to see the capabilities of the robot. We can modify this environment and test the robot further by changing the grade of the incline and decline planes.")]),_vm._v(" "),_c('li',[_vm._v("Once we know that our robot can work in Steps 1 through 4, we want the robot to go through these motions autonomously. Therefore, this step will focus on creating a basic autonomous for the environments in Step 1 and Step 2.")]),_vm._v(" "),_c('li',[_vm._v("Now that our robot can traverse through terrain autonomously, we want it to be able to detect heat within the rubble. We will use an infrared sensor to detect heat, and the robot will have to learn different scenarios.\n\t\t\t\t\t\t\t\t\t\t\t"),_c('ul',[_c('li',[_vm._v("One of these scenarios is having a heat source in one out of three paths; the robot must be able to choose to follow the path with the heat source. If it enters the wrong path, it must have some memory system so that it can retrace its steps back to the crossroads and choose a different path.")]),_vm._v(" "),_c('li',[_vm._v("Another situation is if the heat source is near the robot, but the only path toward the heat is going around a longer path. We want the robot to take the longer path to eventually reach the source. One way of thinking about this problem is having the robot first check for a clear path, then having the robot check for heat.")])])]),_vm._v(" "),_c('li',[_vm._v("We want to combine the environments in Step 4 and Step 6, and then have the robot run autonomously in this environment.")]),_vm._v(" "),_c('li',[_vm._v("(Optional) Automating the spool is the next step after developing a working autonomous program. Instead of manually releasing more tether, the robot would be able to sense when it needs to travel farther and send signals to relax the tension on the tether.")])])]),_c('br'),_c('br'),_vm._v(" "),_c('h1',[_vm._v("Meeting 6: Controls, Motors, and Presentation")]),_vm._v(" "),_c('p',[_vm._v("\n\t\t\t\t\t\t\t\t\tWe began thinking about how we would present our robot during the demonstration, so we planned out how we wanted our board to be formatted. We decided to create a maze from 2’x3’ plywood, and then add pieces on our maze using pegs that we could potentially move around so that we could adjust the maze to any configuration. We drew the grid on our plywood and began thinking about materials for creating the walls of our maze. We also mapped out an ideal maze that we could default to for demonstration purposes. "),_c('br'),_c('br'),_vm._v("\n\n\t\t\t\t\t\t\t\t\tWe also tested our Arduino Nano to try to light up an LED. However, we realized that we needed an H-Bridge to drive our motors to control the voltage going into the Arduino. There were many failed attempts to do so over the next few meetings. "),_c('br'),_c('br'),_vm._v("\n\n\t\t\t\t\t\t\t\t\tWe also realized that our original drone motors’ speed was too large at 12000 RPM; it would be too difficult to gear down our motors to approximately 100 RPM. As a result, we researched stepper motors that could do the job and run at much slower speeds. We ultimately decided on new with a planetary gearbox that reduced the speed to around 55 RPM to 100 RPM. We designed a new configuration with motors on opposite ends of our box in order to fit gears, which are necessary because this RPM is still too high for our needs. We need to gear down and reduce another step with a gear from six to ten teeth. "),_c('br'),_c('br'),_vm._v("\n\n\t\t\t\t\t\t\t\t\tWe also updated the website and completed the team section of the website, which includes team biographies and a summary of what we are doing as a team. Now, we have to develop the section of our website where we can upload all of our journals.\n\t\t\t\t\t\t\t\t")]),_c('br'),_c('br'),_vm._v(" "),_c('h1',[_vm._v("Meeting 7: Controls, 3D Prints, Sensors")]),_vm._v(" "),_c('p',[_vm._v("\n\t\t\t\t\t\t\t\t\tWe were finally able to successfully light up the LED through the Arduino Nano. We also began to look at creating a library for the Arduino. We successfully got the H-Bridge to work with the new motors, but we were unable to get the motors to rotate in different directions. Motors were able to rotate in both directions while directly attached to the Arduino, but not through the H-Bridge. After more testing, we figured that it had to do with our transistors. "),_c('br'),_c('br'),_vm._v("\n\n\t\t\t\t\t\t\t\t\tWe continued the design of the robot and created CAD drawings of the hemispheres of the robot. We also created the centerpiece that will hold our Arduino, two motors, and IR Sensors. However, we decided that this design was not going to work so we created a new centerpiece that no longer required the motors to be staggered on opposite ends of the centerpiece. We either need to purchase or print gears to get the right speed. Our first 3D print of the Hemisphere was created and successful, this is the outermost casing that will be house everything else. We will continue to print more components of our robot. "),_c('br'),_c('br'),_vm._v("\n\n\t\t\t\t\t\t\t\t\tWe decided to implement a system of controls, where we would control the robot through a Microsoft or Xbox controller. This seemed like the most logical way to control the movement of the robot. "),_c('br'),_c('br'),_vm._v("\n\n\t\t\t\t\t\t\t\t\tWe also began testing the IR sensors, but we were unsure whether or not we would use them because it seemed extremely difficult to map with only one IR sensor. We considered adding a second sensor to better help the robot find its location.\n\t\t\t\t\t\t\t\t")]),_c('br'),_c('br')])])],1)])])}
